#effective-month {
  margin-bottom: 2rem;
}

#effective-month .btn-light {
  background-color: white;
  border: 1px solid rgb(103, 65, 134);
  border-radius: 0.3rem;
  color: rgb(103, 65, 134);
  padding: 1rem;
  width: 13rem;
}

#quote-calculator #effective-month label {
  margin-top: 1rem;
  margin-bottom: 1rem;
}

#effective-month .dropdown.bootstrap-select.form-control {
  height: 3rem;
}

#effective-month .dropdown.bootstrap-select.form-control button {
  line-height: 1rem;
}

#effective-month .dropdown.bootstrap-select.form-control > .dropdown-menu.show {
  min-width: 7rem !important;
  max-width: 7rem !important;
  background-color: white;
  border: 1px solid rgb(103, 65, 134);
  border-radius: 0.3rem;
  color: rgb(103, 65, 134);
  padding-top: 0;
  padding-bottom: 0;
}

#effective-month .dropdown.bootstrap-select.form-control > .dropdown-menu.show .dropdown-item.selected {
  background-color: rgb(103, 65, 134);
}

#effective-month .dropdown.bootstrap-select.form-control > .dropdown-menu.show .dropdown-item:not(.selected) {
  color: rgb(103, 65, 134);
}

ul.dropdown-menu li:first-child {
  display: none;
}

.filter-option-inner-inner {
  color: #999 !important;
}

.filter-option-inner-inner.not-placeholder {
  color: rgb(103, 65, 134) !important;
}

@media (max-width: 767px) {
  #effective-month .dropdown.bootstrap-select.form-control button {
    width: 100%;
  }

  #effective-month .dropdown.bootstrap-select.form-control > .dropdown-menu.show {
    min-width: 100% !important;
    max-width: 100% !important;
  }
}
