#employee-count {
  margin-bottom: 2rem;
}

#employee-count input {
  width: 5rem;
}

#employee-count .col-4 {
  padding-left: 0;
  padding-right: 0;
}

#employee-count small {
  display: block;
  color: #CFC4D8;
  line-height: 1.3;
  margin: 1rem 0;
}

#employee-count label.value {
  vertical-align: top;
  line-height: 2.5rem;
  margin-bottom: 0;
}

#employee-count-value.placeholder {
  color: #999 !important;
}

@media (max-width: 767px) {
  #zip {
    margin-bottom: 1rem;
  }

  #zip input {
    margin-top: 1rem;
    width: 100%;
  }

  #employee-count small {
    font-size: 10px;
  }
}
