#zip {
  margin-bottom: 2rem;
}

#zip input {
  width: 7rem;
}

.tooltip-inner {
  color: #c22423;
  background-color: #e9bebc;
  text-align: left;
  max-width: 305px;
  padding: 0.5rem;
}

.tooltip-inner p {
  line-height: 1.3;
  margin-bottom: 0;
}

.tooltip.bs-tooltip-bottom .arrow::before {
  border-bottom-color: #e9bebc !important;
}

.tooltip.bs-tooltip-top .arrow::before {
  border-top-color: #e9bebc !important;
}

.tooltip.bs-tooltip-left .arrow::before {
  border-left-color: #e9bebc !important;
}

.tooltip.bs-tooltip-right .arrow::before {
  border-right-color: #e9bebc !important;
}

.arrow .arrow {
  width: 1.4rem;
  height: 0.8rem;
}

.arrow .arrow::before {
  border-width: 0.4rem 1.4rem 0.4rem 0;
}

#zip-error.has-error input {
  color: #c22423 !important;
  border-color: #c22423 !important;
}

@media (max-width: 767px) {
  #zip {
    margin-bottom: 1rem;
  }

  #zip input {
    margin-top: 1rem;
    width: 100%;
  }

  #zip-error {
    width: 100%;
  }
}
