#quote button.btn-dimmed,
.btn:disabled {
  opacity: .4;
}

#quote button, a.btn-final-quote, a.btn-final-quote:hover, a.btn-primary:not(:disabled):not(.disabled).btn-final-quote:active {
  font-size: 1rem;
  padding: 1rem 3rem;
  background-color: rgb(103, 65, 134);
  border: none;
}

#quote .modal-dialog.premiums {
  min-width: 80%;
}

#quote .modal-dialog.premiums .modal-title.congratulations {
  font-size: 4.5rem;
  font-weight: 300;
  text-transform: uppercase;
  color: rgb(103, 65, 134);
}

#quote .modal-dialog.premiums .modal-body {
  padding: 3rem;
}

#quote .modal-dialog.premiums .modal-body .container-fluid {
  padding: 0;
}

#quote .modal-dialog.premiums h1 + .lead {
  font-size: 2rem;
  color: rgb(103, 65, 134);
}

#quote .modal-dialog.premiums .plans {
  margin-bottom: 1.5rem;
}

#quote .modal-dialog.premiums .plans .hmo,
#quote .modal-dialog.premiums .plans .pos {
  border: 2px solid rgb(103, 65, 134);
  border-top-left-radius: 0.25rem;
  border-top-right-radius: 0.25rem;
  box-shadow: 0 10px 20px rgba(0, 0, 0, 0.1);
}

#quote .modal-dialog.premiums .plans h2 {
  font-size: 3rem;
  padding: 1rem;
  color: white;
  background-color: rgb(103, 65, 134);
}

.details {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
}

#quote .modal-dialog.premiums .details div:first-child p {
  padding-left: 0.5rem;
  padding-right: 0;
}

#quote .modal-dialog.premiums .details p {
  color: rgb(103, 65, 134);
  padding-right: 0.5rem;
}

#quote .modal-dialog.premiums .call-to-action {
  width: 50%;
  color: rgb(107, 103, 119);
  margin-left: auto;
  margin-right: auto;
}

#quote .modal-dialog.premiums .call-to-action .btn.btn-primary {
  color: #ffffff;
  background-color: rgb(103, 65, 134);
  border: none;
  margin-left: auto;
  margin-right: auto;
  font-size: 1rem;
  padding: 1rem 4rem;
}

#quote .btn-get-quote {
  font-size: 1rem;
  padding: 1rem 4rem;
}

.divider {
  background-color: #D9D0E1;
  width: 100%;
  height: 2px;
  margin-left: 0.5rem;
  margin-right: 0.5rem;
  margin-top: -0.5rem;
}

.details:last-child .divider {
  display: none;
}

#quote .lead {
  font-weight: 400;
}

#quote-modal {
  padding: 0 !important;
}

#quote .disclaimer {
  color: #767482;
  margin-bottom: 2rem;
}

@media (max-width: 991px) {
  #quote .modal-dialog.premiums .modal-title.congratulations {
    font-size: 3.5rem;
  }
}

@media (max-width: 767px) {
  #quote .modal-dialog.premiums .modal-body {
    padding: 2rem 0.5rem;
  }

  #quote .modal-dialog.premiums .modal-title.congratulations {
    font-size: 2rem;
  }

  #quote .modal-dialog.premiums h1 + .lead {
    font-size: 1.25rem;
  }

  #quote .modal-dialog.premiums .plans {
    margin-bottom: 0;
  }

  #quote .modal-dialog.premiums .plans > div {
    margin-bottom: 2rem;
  }

  #quote .modal-dialog.premiums .call-to-action .btn.btn-primary,
  #quote .modal-dialog.premiums .call-to-action,
  #quote .btn-get-quote {
    width: 100%;
  }

  #quote {
    width: 100% !important;
    padding-left: 0 !important;
    padding-right: 0 !important;
  }
}
