.color-bar {
  height: 4px;
  display: flex;
  border-top-left-radius: 4px;
  border-top-right-radius: 4px;
  overflow: hidden;
}

.color-bar div {
  width: 25%;
  height: 100%;
}

.color-bar #color-1 {
  background-color: #E5B54F;
}

.color-bar #color-2 {
  background-color: #009CD1;
}

.color-bar #color-3 {
  background-color: #7EBC37;
}

.color-bar #color-4 {
  background-color: #674186;
}

#quote .color-bar {
  border-top-left-radius: .3rem;
  border-top-right-radius: .3rem;
}

#quote .color-bar div {
  width: 50%;
}

#quote .color-bar #color-1,
#quote .color-bar #color-4 {
  display: none;
}
