#employee-birthdates h4 {
  display: inline-block;
  width: 20%;
  margin-bottom: 1rem;
  margin-right: 1rem;
  padding: 0rem;
  color: rgb(103, 65, 134);
  font-size: 1.25rem;
}

#employee-birthdates .fa-trash-alt {
  color: #c2c2c2;
}

#employee-birthdates h3 {
  color: rgb(103, 65, 134);
  margin-bottom: 2rem;
}

#employee-birthdates .spacer,
#employee-birthdates .row-number {
  display: inline-block;
  width: 10%;
}

#employee-birthdates .month {
  width: 20%;
  margin-right: 1rem;
}

#employee-birthdates .date {
  width: 20%;
  margin-right: 1rem;
}

#employee-birthdates .year {
  width: 25%;
}

#employee-birthdates button {
  width: 15%;
}

#employee-birthdates .employee-row {
  display: flex;
}

.show-birthdates {
  justify-content: center;
}

.show-birthdates .btn,
.show-birthdates .btn-primary:not(:disabled):not(.disabled).active,
.show-birthdates .btn-primary:not(:disabled):not(.disabled):active,
.show > .show-birthdates .btn-primary.dropdown-toggle {
  font-size: 1rem;
  padding: 1rem 4rem;
  background-color: rgb(103, 65, 134);
  border: none;
}

@media (max-width: 767px) {
  #employee-birthdates h4 {
    width: 24%;
  }

  #employee-birthdates .spacer,
  #employee-birthdates .row-number {
    display: none;
  }

  #employee-birthdates .month {
    width: 24%;
    margin-right: 1rem;
  }

  #employee-birthdates .date {
    width: 24%;
    margin-right: 1rem;
  }

  #employee-birthdates .year {
    width: 32%;
  }

  #employee-birthdates button {
    width: 10%;
  }

  .show-birthdates .btn {
    margin: 0 15px;
    width: 100%;
  }
}
